@import "./src/styles/variables";

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 21px;
}

.wrapper {
  display: flex;
  align-items: center;
}

.change_theme, .logo {
  margin-right: 15px;
  cursor: pointer;
}

.logo {
  svg {
    width: 49px;
    height: 49px;
  }
}

.title {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 19px;
  color: $primary-color;
}

