@import "../../../../styles/variables";

.this__day {
  padding: 15px;
  max-width: 300px;
  width: 100%;
  background: var(#{--components-background-default});
  box-shadow: $box-shadow;
  border-radius: $border-radius;
}

.top__block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 23px;
  img {
    width: 116px;
    height: 116px;
  }
  .this__temp {
    font-size: 72px;
    font-weight: 500;
    color: $primary-color;
  }
  .this__day_name {
    font-size: 30px;
    color: var(#{--text-color-default});
  }
}

.bottom__block {
  .this__add_info,
  .this__city {
    color: $base-color;
    padding-top: 5px;
    font-size: 19px;
  }
}

.bottom__block {
  .this__city {
    font-size: 24px;
  }
}