@import '../../../styles/variables';

.days {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 15px;
  width: 100%;
  background-color: var(#{--components-background-default});
  box-shadow: $box-shadow;
  border-radius: 0 0 $border-radius $border-radius;
}

.card {
  display: flex;
  flex-direction: column;
  background-color: var(#{--card-background-default});
  border-radius: 10px;
  padding: 7px;
  width: 112px;
  transition: 0.3s;
  cursor: pointer;

  .day {
    margin-bottom: 5px;
    font-size: 14px;
    color: var(#{--text-color-default});
  }

  .day__info {
    font-size: 10px;
    margin-bottom: 9px;
    color: $base-color;
  }
  .img {
    margin-bottom: 9px;
  }

  .temp__day {
    font-size: 15px;
    color: var(#{--text-color-default});
  }

  .temp__night {
    font-size: 9px;
    color: $base-color;
    margin-bottom: 5px;
  }
  .info {
    font-size: 10px;
    color: $base-color;
  }

  &:hover {
    box-shadow: var(#{--card-shadow-default});
  }
}